@import url(https://fonts.googleapis.com/css2?family=Righteous&family=Montserrat&family=Train+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  box-sizing: border-box;
}

html,
body,
#root,
#root > div,
div[data-reactroot] {
  height: 100%;
  padding: 0;
  margin: 0;
}


body {
  margin: 0;
  padding: 0;
  background: #333;
  -webkit-justify-content: center;
          justify-content: center;
}
.hero {
  background: rgb(240, 211, 115);
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: left;
          justify-content: left;
  height: 100vh;
  width: 100vw;
}

.header-p {
  font-family: 'Righteous', cursive;
  font-size: 25px;
}

.heroheader {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  background: rgb(240, 211, 115);
}

.form-control {
  width: 50% !important;
  -webkit-align-content: center !important;
          align-content: center !important;
}

.content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100vh;
  width: 70vw;
  margin: auto;
  -webkit-transform-origin: left;
          transform-origin: left;
  -webkit-animation: reveal 1s ease-in-out forwards;
          animation: reveal 1s ease-in-out forwards;
}

h1 {
  font-family: 'Train One', cursive;
  font-size: 100px;
  line-height: 0.9;
  margin-bottom: 0px;
}

.aboutheader {
  font-size: 30px;
  color: rgb(0, 0, 0);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
  margin: auto;
  -webkit-animation: reveal 1s ease-in-out forwards;
          animation: reveal 1s ease-in-out forwards;
}

p, .about-p {
  font-size: 15px;
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  color: rgb(0, 0, 0);
}

.contact-form, .navbar {
  font-size: 15px;
  font-family: 'Montserrat', sans-serif;
}

@-webkit-keyframes reveal {
  0% {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
  100% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
}

@keyframes reveal {
  0% {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
  100% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
}

.text-center, .app-link, .card-p {
  font-size: 15px;
  font-family: "Montserrat", sans-serif;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 0.5s ease;
  background-color: #1d0b0b;
}

.photo:hover .overlay {
  opacity: 1;
}

.photo {
  position: relative;
  width: 100%;
}

.image {
  display: block;
  width: 100%;
  height: auto;
}

.app-link {
  color: #ffffff;
}

.text {
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
}

.footer {
    margin-top: 30px;
    height: 50px;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.515);
    color: white;
    text-align: center;
  }

.footer-icons {
    margin-top: 10px;
    color:rgb(240, 211, 115);
}

.footer-icons:hover {
  -webkit-transform:scale(1.1);
          transform:scale(1.1)
}

